main {
    max-width: 90%;
    min-width: 90%;
    margin: auto;
}



.list-of-data {
    background: linear-gradient(180deg, rgba(70, 36, 1) 0%, rgba(9, 9, 121, 1) 35%, rgba(0, 212, 255, 1) 100%);
    /* background-color: rgba(255, 0, 255, 0.5); */
}

header {
    text-align: center;
}


td img {
    width: 100px;
    border-radius: 50%;
}

tr {
    text-transform: uppercase;
    background-color: cadetblue;
}

th {
    font-size: 16px;
}

.open{
    width: 300px;
    position: fixed;
    margin: auto;
    background: rgb(255, 255, 255);
    border: 2px solid lightskyblue;
    top: 20%;
    left: 30%;
    border-radius: 8px;
    box-shadow: 15px 15px 15px lightsteelblue;
}
/* 
.popup {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
} */

input {
    margin-block-end: 1rem;
    padding: 1rem;
    margin-block-end: 1rem;
    border-color: fuchsia;
    border-radius: 6px;
}

label {
    font-size: 1rem;
    text-transform: uppercase;
    color: fuchsia;
    font-weight: 700;
}

.to-deleteEl p {
    font-size: 2rem;
}

.to-deleteEl p:first-of-type {
    color: red;
}

q {
    color: blue;
}

thead {
    font-size: 1.25em;
    background: #fffffe;
}

.buttons {
    display: flex;
    gap: 1rem;
    border-radius: 6px;
}

.btn {
    padding: 10px;
    border-radius: 50%;
    background-color: pink;
    font-weight: 700;
}

.to-delete {
    width: 50%;
    height: 50%;
    background-color: rgb(255, 208, 0, 0.9);
    border-radius: 6px;
    position: fixed;
    top: 20%;
    left: 30%;
}

.to-deleteEl {
    text-align: center;
}

.add {
    padding: 10px;
    border-radius: 50%;
}

